.presentationContainer {
  position: absolute;
  left: 0;
  top: 640px;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 600px;
  background-color: #e3e3e3;
}

.thought {
  display: flex;
  background-color: #323232;
  padding: 20px;
  border-radius: 30px;
  min-width: 40px;
  max-width: 600px;
  min-height: 40px;
  margin: 20px;
  margin-right: 50px;
  position: relative;
  align-items: center;
  justify-content: center;
  text-align: center;
}
.thought:before,
.thought:after {
  content: "";
  background-color: #323232;
  border-radius: 50%;
  display: block;
  position: absolute;
  z-index: -1;
}
.thought:before {
  width: 44px;
  height: 44px;
  top: -12px;
  left: 28px;
  box-shadow: -50px 30px 0 -12px #323232;
}
.thought:after {
  bottom: -10px;
  right: 26px;
  width: 30px;
  height: 30px;
  box-shadow: 40px -34px 0 0 #323232, -28px -6px 0 -2px #323232,
    -24px 17px 0 -6px #323232, -5px 25px 0 -10px #323232;
}

